
import { defineComponent } from 'vue';
import {GetData} from "@/http";
import * as XLSX from 'ts-xlsx';
import LoadingIconview from '@/components/common/LoadingIcon.vue';

export default defineComponent({
    name: 'ManageUsermodal',
	components:{
		LoadingIconview
	},
    data() {
        return {
            message: "",
            workers: [],
            showTable: false,
            showModal: false,
            loadingstatus: false,
            zoomPic: '',
            searchWord: '',
            dict: 'photo',
            filetxt: 'ファイルが選択されていません',
            TSheet: '新規ユーザー追加',
            loadingtxt: '',
            items: [''],
            http:new GetData(),

            uploadFile: {}
        };
    },
    methods:{
        Changecsv(e:Event){
            const target = e.target as HTMLInputElement;
            if(!target.files){
                return;
            }
            this.uploadFile = target.files;
            this.filetxt = target.files[0].name;
        },
        Addcsv(e:Event) {
            const target = e.target as HTMLInputElement;
            const http = new GetData();
            this.loadingstatus = true;
            this.loadingtxt = '';
            const sleep = (msec:any) => {new Promise(resolve => setTimeout(resolve, msec))};

            this.parseSheet(this.uploadFile, this.TSheet, async (result:any) => {
                var devidedata = this.DevideData(result);
                var roupcount = 0;
                for(var key in devidedata){
                    roupcount += 1;
                    for(var key2 in devidedata[key]){
                        http.Postcommon(
                            '/api/manageuser/import',
                            {
                                "name":devidedata[key][key2][0],
                                "mail_address":devidedata[key][key2][1],
                            },
                            (res:any) => {
                                this.loadingstatus = false;
                                this.loadingtxt = `${roupcount}`;
                            },
                            (res:any) => {
                                this.loadingstatus = false;
                                this.loadingtxt = '取り込みに失敗しました。';
                            }
                        );
                    }
                    await sleep(1000);
                }
            });
        },

        //[[名前,メアド], [名前,メアド], ...] 形式の配列を作る
        parseSheet(file:any, sheetname:string, callback:any) {
            var reader = new FileReader(),
            blob = new Blob(file);
            reader.onload = (e) => {
                const target:any = e.target;
                var unit8 = new Uint8Array(target.result),
                workbook = XLSX.read(unit8, {type: "array"}),
                sheet:any = XLSX.utils.sheet_to_json(workbook.Sheets[sheetname], {header: 1}),
                result = [];

                for (var i = 0; i < sheet.length; i++) {
                    var row:any = sheet[i];

                    if(i != 0){
                        result.push(row);
                    }
                }
                callback(result);
            };
            reader.readAsArrayBuffer(blob);
        },
        DevideData(result:any){
            var divcount = 0;
            var divgroup = 0;
            var jsonsendarr:any = [];
            var jsonsend:any = [];
            result.forEach((each:any) => {
                divcount += 1;
                if(divcount % 20 == 0){
                    divgroup += 1;
                    jsonsend = [];
                }
                jsonsend[divcount] = each;
                jsonsendarr[divgroup] = jsonsend;
            });
            return jsonsendarr;
        }
    }
});
